.contacts {
  height: 100%;
  @include flex(flex-start, normal, row, wrap);

  &__left, &__right {
    width: 50%;
    @include md {
      width: 100%; } }
  &__right {
    background-color: $black-light;
    @include flex(center, center);
    padding: 15px;
    @include md {
      display: block; } }
  &__img {
    height: 100%;
    @include cover;
    background-color: $gray;
    @include md {
      padding-top: 50%; }
    @include xs {
      padding-top: 70%; } } }


