.contacts-block {
  color: $white;
  width: 100%;
  max-width: 625px;
  &__title {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__contacts {
    margin-bottom: 35px;
    @include flex(flex-start, normal, row, wrap);
    @include md {
      margin-bottom: 20px; }
    .contacts-meta {
      width: 50%;
      padding-right: 15px;
      margin-bottom: 45px;
      @include xl {
        margin-bottom: 30px; }
      @include md {
        margin-bottom: 15px; } } } }

.contacts-meta {
  &__title {
    font-weight: 300;
    text-transform: uppercase;
    color: $gray-100;
    margin-bottom: .5em; }
  &__text {
    font-size: em(18);
    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } } } }
